'use strict';

// MODULES //

import isDateObject from '@stdlib/assert-is-date-object';
import { isPrimitive as isInteger } from '@stdlib/assert-is-integer';
import isLeapYear from '@stdlib/assert-is-leap-year';
import format from '@stdlib/error-tools-fmtprodmsg';
import currentYear from '@stdlib/time-current-year';


// VARIABLES //

var NON_LEAP_YEAR = 8760|0; // 365 * 24
var LEAP_YEAR = 8784|0; // 366 * 24


// MAIN //

/**
* Returns the number of hours in a year.
*
* @param {(integer|Date)} value - year or `Date` object
* @throws {TypeError} must provide either an integer or a `Date` object
* @returns {integer} number of hours in a year
*
* @example
* var num = hoursInYear();
* // returns <number>
*
* @example
* var num = hoursInYear( 2016 );
* // returns 8784
*
* @example
* var num = hoursInYear( 2017 );
* // returns 8760
*/
function hoursInYear( value ) {
	var yr;
	if ( arguments.length ) {
		if ( isDateObject( value ) ) {
			yr = value.getFullYear();
		} else if ( isInteger( value ) ) {
			yr = value;
		} else {
			throw new TypeError( format( '1QHLR', value ) );
		}
	} else {
		// Note: cannot cache as application could cross over into a new year:
		yr = currentYear();
	}
	if ( isLeapYear( yr ) ) {
		return LEAP_YEAR;
	}
	return NON_LEAP_YEAR;
}


// EXPORTS //

export default hoursInYear;
